<template>
	<div>
    <div>
        <el-form ref="form" :model="form" label-width="120px">
  
  <el-form-item label="Original Language">
    <el-select v-model="form.ori_lang" placeholder="Original Language">
      <el-option label="EN" value="EN"></el-option>
      <el-option label="CN" value="CN"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="Oriented Language">
    <el-select v-model="form.ore_lang" placeholder="Oriented Language">
      <el-option label="EN" value="EN"></el-option>
      <el-option label="CN" value="CN"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="DDL">
    <el-date-picker
      v-model="form.ddl"
      type="datetime"
      placeholder="Choose Suitable DDl">
    </el-date-picker>
  </el-form-item>
  <el-form-item label="Customized Tags">
    <el-tag
  :key="tag"
  v-for="tag in dynamicTags"
  closable
  :disable-transitions="false"
  @close="handleClose(tag)">
  {{tag}}
</el-tag>
<el-input
  class="input-new-tag"
  v-if="inputVisible"
  v-model="inputValue"
  ref="saveTagInput"
  size="small"
  @keyup.enter.native="handleInputConfirm"
  @blur="handleInputConfirm"
>
</el-input>
<el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
</el-form-item>
  <el-form-item label="Title">
    <el-input v-model="form.title"></el-input>
  </el-form-item>
  <el-form-item label="Content">
    <el-input type="textarea"
            :autosize="{ minRows: 5, maxRows: 30}"
            placeholder="Place Content here, using txt file or type."
            v-model="form.content"></el-input>
    <el-upload
        :auto-upload="false"
        :on-change="elInFile"
        ref="upload"
        class="upload-demo"
        accept="*/*">
    <el-button slot="trigger" size="mini" type="success" plain>Choose File</el-button>
    <i slot="tip" class="el-upload__tip el-icon-info">Please Choose .txt file</i>
    </el-upload>
        Number of words: {{form.content.length}}
  </el-form-item>
  <el-form-item label="Abstract">
    <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}"  v-model="form.abstract"></el-input>
  </el-form-item>
  <el-form-item label="Fee">
    <el-tooltip class="item" effect="dark" content="6 dollars.100 words" placement="top-start">
      <el-input  :disabled="true" v-model="form.fee"></el-input>
    </el-tooltip>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onSubmit">Create An Order!</el-button>
    <el-button @click="resetPage()">Reset</el-button>
  </el-form-item>
</el-form>
    </div>
    
		
	</div>
</template>
<script>
import { createAnOrder } from '../../api/api';
import Qs from 'qs'
	export default {
		name: 'importTxt',
		data() {
			return {
                files: null,
                form_o: {
                title: '',
                ori_lang: '',
                ore_lang: '',
                ddl: '',
                tags: [],
                content: '',
                abstract: '',
                fee:0,
                },
                form: {
                title: '',
                ori_lang: '',
                ore_lang: '',
                ddl: '',
                tags: [],
                content: '',
                abstract: '',
                fee:0,
                },
                dynamicTags: ['news', 'tourism', 'en'],
                inputVisible: false,
                inputValue: ''
            
			};
		},
		methods: {
      resetPage(){
        let new_form = {...this.form_o}
        this.form = new_form;
      },
      // submit
      onSubmit(){
        var user = sessionStorage.getItem('user');
        user = JSON.parse(user);
        let uid = user.uid
        let param = this.form
        param['uid'] = uid; 
        param.tags = this.dynamicTags.join(';') // convert array to string
        param = Qs.stringify(param)
        createAnOrder(param).then(data => {
            let { msg,code,status_code, oid } = data;
            if (code !== 200) {
              this.$message.error(`Server Error`);
            } else {
              if(status_code === 0){
                this.$message({
                  message:msg + `, oid:${oid}`,
                  type: 'success'
                });
                this.$router.push({ name: 'HomePage'})
              }else{
                this.$message.error(msg);
              }
            }
          });
      },
      // tags
      handleClose(tag) {
        this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
      },

      showInput() {
        this.inputVisible = true;
        this.$nextTick(_ => {
          this.$refs.saveTagInput.$refs.input.focus();
        });
      },

      handleInputConfirm() {
        let inputValue = this.inputValue;
        if (inputValue) {
          this.dynamicTags.push(inputValue);
        }
        this.inputVisible = false;
        this.inputValue = '';
      },
      // how much per 100 words
      cal_fee(num){
          this.form.fee = 0.06 * num
      },
			/**
			 * input-fileè°ƒç”¨æ­¤å‡½æ•°æ—¶ï¼Œé»˜è®¤ä¼ å…¥"$event"
			 * @param e {@link Object}ï¼š$eventäº‹ä»¶å¯¹è±¡ï¼›
			 */
			inFile(e) {
				this.files = (e.target || e.srcElement).files;  //èŽ·å–"input-file"çš„FileListå¯¹è±¡
				if (!this.files || !this.files.length)
					return;
				for (let i = 0, len = this.files.length; i < len; i++)
					this.read(this.files[i]);
			},
			/**
			 * æ–‡ä»¶çŠ¶æ€æ”¹å˜æ—¶çš„é’©å­ï¼Œæ·»åŠ æ–‡ä»¶ã€ä¸Šä¼ æˆåŠŸå’Œä¸Šä¼ å¤±è´¥æ—¶éƒ½ä¼šè¢«è°ƒç”¨ã€‚
			 * @param f {@link Object}ï¼šå½“å‰ä¸Šä¼ çš„æ–‡ä»¶ï¼›
			 * @param fs {@link Array}ï¼šå½“å‰æ–‡ä»¶åˆ—è¡¨ï¼›
			 */
			elInFile(f, fs) {
				this.read(f.raw);
			},
			/**
			 * è§£æž
			 * @param f {@link File}
			 */
			read(f) {
				let rd = new FileReader();
				rd.onload = e => {  //this.readAsArrayBufferå‡½æ•°å†…ï¼Œä¼šå›žè°ƒthis.onloadå‡½æ•°ã€‚åœ¨è¿™é‡Œå¤„ç†ç»“æžœ
					let cont = rd.reading({encode: 'UTF-8'});
                    // console.log('æ–‡ä»¶å†…å®¹ï¼š', cont);
                    this.form.content += '\n'
                    this.form.content += cont;
                    this.cal_fee(this.form.content.length)
				};
				rd.readAsBinaryString(f);
			}
		},
		beforeCreate() {
			/**
			 * è¯»å–æ–‡ä»¶ï¼ˆè‡ªå®šä¹‰å‡½æ•°ï¼‰
			 * @param pms {@link Object}ï¼šå‚æ•°ï¼š
			 *  @param pms.encode {@link String}ï¼šç¼–ç æ ¼å¼ï¼š
			 * @return {@link String}ï¼šæ–‡æœ¬å†…å®¹ï¼›
			 */
			FileReader.prototype.reading = function ({encode} = pms) {
				let bytes = new Uint8Array(this.result);    //æ— ç¬¦å·æ•´åž‹æ•°ç»„
				let text = new TextDecoder(encode || 'UTF-8').decode(bytes);
				return text;
			};
			/* é‡å†™readAsBinaryStringå‡½æ•° */
			FileReader.prototype.readAsBinaryString = function (f) {
				if (!this.onload)       //å¦‚æžœthisæœªé‡å†™onloadå‡½æ•°ï¼Œåˆ™åˆ›å»ºä¸€ä¸ªå…¬å…±å¤„ç†æ–¹å¼
					this.onload = e => {  //åœ¨this.onloadå‡½æ•°ä¸­ï¼Œå®Œæˆå…¬å…±å¤„ç†
                        let rs = this.reading();
                        this.content = rs;
						// console.log(rs);
					};
				this.readAsArrayBuffer(f);  //å†…éƒ¨ä¼šå›žè°ƒthis.onloadæ–¹æ³•
			};
		}
	}
</script>
<style scoped>
	.in-file {
		text-align: center;
	}
  .el-tag + .el-tag {
    margin-left: 10px;
  }
  .button-new-tag {
    margin-left: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .input-new-tag {
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
  }
  .item {
      margin: 4px;
  }
</style>



