import Login from './views/Login.vue'
import Home from './views/Home.vue'
import Index from './views/Index.vue'
import Homepage from './views/list/homepage.vue'
import Market from './views/list/Market.vue'
import WorkPanel from './views/list/WorkPanel.vue'
// 
import UploadTask from './views/list/uploadTask.vue'
import Register from "./views/Register";

let routes = [
    {
        path: '/',
        name: 'mainpage',
        redirect: '/index',
        hidden: true
    },
    {
        path: '/index',
        component: Index,
        name: 'mainpage_index',
        hidden: true
    },
    {
        path: '/register',
        component: Register,
        name: 'register',
        hidden: true
    },
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/',
        component: Home,
        name: '',
        leaf: true,//åªæœ‰ä¸€ä¸ªèŠ‚ç‚¹
        iconCls: 'el-icon-menu', //å›¾æ ‡æ ·å¼class
        children: [
            {
                path: '/homepage', component: Homepage, name: 'HomePage', meta: {
                    keepAlive: true // éœ€è¦è¢«ç¼“å­˜
                }
            },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        leaf: true,//åªæœ‰ä¸€ä¸ªèŠ‚ç‚¹
        iconCls: 'el-icon-shopping-bag-2', //å›¾æ ‡æ ·å¼class
        children: [
            {path: '/market', component: Market, name: 'Market'},
        ]
    },
    {
        path: '/',
        component: Home,
        name: 'å·¥ä½œå°',
        leaf: true,
        iconCls: 'el-icon-s-platform',
        children: [
            {path: '/panel', component: WorkPanel, name: 'Panel'}
        ]
    },

    {
        path: '/',
        component: Home,
        name: 'SubmitOrder',
        leaf: true,
        iconCls: 'el-icon-upload2',
        children: [
            {path: '/submit_order', component: UploadTask, name: 'SubmitOrder'}
        ],
        // hidden: true
    }

];

export default routes;


