import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'

Vue.use(Vuex)

// åº”ç”¨åˆå§‹çŠ¶æ€
const state = {
    count: 10
}

// å®šä¹‰æ‰€éœ€çš„ mutations
const mutations = {
    INCREMENT(state) {
        state.count++
    },
    DECREMENT(state) {
        state.count--
    }
}

// åˆ›å»º store å®žä¾‹
export default new Vuex.Store({
    actions,
    getters,
    state,
    mutations
})


